import { NextRequest, NextResponse } from 'next/server';
import connectDB from '@/lib/mongodb';
import User from '@/lib/models/User';
import mongoose from 'mongoose';

export async function POST(request: NextRequest) {
  console.log('ðŸ”µ REGISTER API CALLED');
  console.log('ðŸ“Š Current mongoose state:', mongoose.connection.readyState);
  
  try {
    // Connect to database
    console.log('ðŸŸ¡ Connecting to database...');
    await connectDB();
    console.log('âœ… Database connected, state:', mongoose.connection.readyState);

    // Parse request
    const body = await request.json();
    const { email, password, name } = body;

    console.log('ðŸ“¥ Registration data:', { email, name, passwordLength: password.length });

    // Validation
    if (!email || !password || !name) {
      return NextResponse.json(
        { error: 'Email, password, and name are required' },
        { status: 400 }
      );
    }

    // Check for existing user
    console.log('ðŸ” Checking for existing user...');
    const existingUser = await User.findOne({ email: email.toLowerCase().trim() });
    if (existingUser) {
      console.log('âŒ User already exists');
      return NextResponse.json(
        { error: 'User already exists with this email' },
        { status: 400 }
      );
    }
    console.log('âœ… No existing user found');

    // Create user
    console.log('ðŸ‘¤ Creating user...');
    const user = await User.create({
      email: email.toLowerCase().trim(),
      password,
      name: name.trim(),
    });
    console.log('âœ… User created with ID:', user._id);

    // Generate token
    console.log('ðŸ”‘ Generating JWT token...');
    const jwt = await import('jsonwebtoken');
    const token = jwt.sign(
      { userId: user._id.toString(), email: user.email, name: user.name },
      process.env.JWT_SECRET!,
      { expiresIn: '7d' }
    );

    console.log('ðŸŽ‰ REGISTRATION SUCCESSFUL!');

    return NextResponse.json({
      message: 'User registered successfully',
      token,
      user: { id: user._id, email: user.email, name: user.name },
    }, { status: 201 });

  } catch (error: any) {
    console.error('ðŸ’¥ REGISTRATION ERROR:');
    console.error('Error name:', error.name);
    console.error('Error message:', error.message);
    console.error('Error code:', error.code);
    console.error('Full error:', error);
    
    if (error.name === 'ValidationError') {
      const errors = Object.values(error.errors).map((err: any) => err.message);
      return NextResponse.json(
        { error: 'Validation failed: ' + errors.join(', ') },
        { status: 400 }
      );
    }
    
    if (error.code === 11000) {
      return NextResponse.json(
        { error: 'Email already exists' },
        { status: 400 }
      );
    }

    return NextResponse.json(
      { error: error.message || 'Registration failed' },
      { status: 500 }
    );
  }
}